	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.economist.com/world/europe/displayStory.cfm?story_id=1291344\">Economist.com | The continent awash</a> is a good summary of the flood situation and its aftermath (except that it's a <i>Jahrtausendflut</i>, not a <i>Jahrhundertflut</i>). Whether he deserves it or not, 4 weeks before the election Schröder is getting a boost in the polls as a result of the flood crisis, pulling the SPD nearly even with the CDU/CSU after trailing by 5 points a month ago. </p>\n<p>Locally the flood has crested, but is sinking very slowly, soaking and weakening the dykes. In Amt Neuhaus and Dannenberg, east of Lüneburg, Bundeswehr troops and volunteers are still shoring up the dykes with sandbags. In Dresden, over 100 of Mama's fellow employees have been severely affected, losing all their household belongings. Her employer is organizing donations.</p>","fields":{"title":"After the deluge, moi?","date":"24 Aug 2002","url":"https://papascott.de/2002-08-24-after-the-deluge-moi/"}}},"pathContext":{"slug":"/2002-08-24-after-the-deluge-moi/","prev":{"fields":{"title":"Random Notes 22 Aug 02","date":"22 Aug 2002","slug":"/2002-08-22-random-notes-22-aug-02/"}},"next":{"fields":{"title":"An Evil Idea","date":"25 Aug 2002","slug":"/2002-08-25-an-evil-idea/"}}}}

